import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { useIntl, FormattedMessage, Link } from 'gatsby-plugin-intl';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Query } from '../../graphql-types';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import { BreadCrumbs } from '../components/breadcrumbs';
import { Separator } from '../components/separator';

const CataloguePage: React.FunctionComponent<{ data: Query }> = ({ data: { allorsCatalogue: catalogue } }) => {
  const intl = useIntl();
  const productCategories = [...catalogue.productCategories].sort((a, b) => a.name && b.name && a.name.localeCompare(b.name));

  return (
    <Layout>
      <SEO title={intl.formatMessage({ id: 'catalogue' })} description={intl.formatMessage({ id: 'catalogue-description' })} />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <BreadCrumbs section="catalogue" />
                  <h1 className="page-title">
                    <FormattedMessage id="catalogue" />
                  </h1>
                  <p className="page-description">
                    <FormattedMessage id="catalogue-description" />
                  </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>
          <Container>
            <Row>
              <Col xl="3" lg="4" xs="12">
                <div className="list-group mb-4">
                  <div className="list-group-item list-group-item-action active">
                    <FormattedMessage id="categories" />
                  </div>
                  {productCategories &&
                    productCategories.map((category) => {
                      return (
                        <Link to={`/${category.slug}`} key={category.slug} className="list-group-item list-group-item-action">
                          {category.name}
                        </Link>
                      );
                    })}
                </div>
              </Col>
              <Col xl="9" lg="8" xs="12">
                <Row>
                  {productCategories &&
                    productCategories.map((category) => {
                      const fluid = category.categoryImage.file.childImageSharp.fluid;
                      return (
                        <Col key={category.slug} md="4" xs="6">
                          <Link to={`/${category.slug}`} key={category.slug} className="product-link">
                            <Card>
                              <Img fluid={fluid} />
                              <Card.Body>
                                <Card.Title>{category.name}</Card.Title>
                              </Card.Body>
                            </Card>
                          </Link>
                        </Col>
                      );
                    })}
                </Row>
              </Col>
            </Row>
          </Container>
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query {
    allorsCatalogue {
      name
      productCategories {
        slug
        name
        categoryImage {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  }
`;

export default CataloguePage;
